// Estilos específicos para la página de categoría
.category-header {
    text-align: center;
    margin-bottom: 2rem;
    
    h1 {
      color: $primary-color;
      margin-bottom: 0.5rem;
    }
    
    p {
      max-width: 800px;
      margin: 0 auto;
    }
  }
  
  .filter-container {
    margin-bottom: 2rem;
    text-align: center;
    
    .filter-button {
      background-color: $secondary-color;
      color: $text-color;
      border: none;
      padding: 0.5rem 1rem;
      margin: 0 0.5rem 0.5rem 0;
      border-radius: 5px;
      cursor: pointer;
      @include transition(all, 0.3s);
      
      &:hover {
        background-color: darken($secondary-color, 10%);
      }
      
      &.active {
        background-color: $primary-color;
        color: white;
      }
    }
  }
  
  .pagination {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    
    .page-item {
      margin: 0 0.25rem;
      
      .page-link {
        display: inline-block;
        padding: 0.5rem 1rem;
        background-color: white;
        color: $primary-color;
        border-radius: 5px;
        @include transition(all, 0.3s);
        
        &:hover {
          background-color: $primary-color;
          color: white;
        }
      }
      
      &.active .page-link {
        background-color: $primary-color;
        color: white;
      }
    }
  }