:root {
  --primary-color: #21344b;
  --secondary-color: #78b9ba;
  --text-color: #333;
  --background-color: #c6c5b4;
  --accent-color: #ce472f;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: Montserrat, Arial, sans-serif;
  line-height: 1.6;
}

h1, h2, h3, h4, h5, h6 {
  color: #21344b;
  margin-bottom: 1rem;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

p {
  margin-bottom: 1rem;
}

a {
  text-decoration: none;
  transition: color .3s;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.recipe-grid {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  display: grid;
}

@media (width <= 768px) {
  .recipe-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}

header {
  color: #fff;
  text-align: center;
  background-color: #21344b;
  padding: 1rem 0;
  box-shadow: 0 2px 5px #0000001a;
}

.logo {
  margin-bottom: .5rem;
  font-size: 2rem;
  font-weight: bold;
}

.hero {
  text-align: center;
  margin-bottom: 3rem;
}

.hero h1 {
  color: #21344b;
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.hero p {
  max-width: 800px;
  margin: 0 auto;
  font-size: 1.2rem;
}

nav {
  background-color: #78b9ba;
  padding: 1rem 0;
}

nav ul {
  justify-content: center;
  list-style-type: none;
  display: flex;
}

@media (width <= 768px) {
  nav ul {
    flex-direction: column;
    align-items: center;
  }
}

nav ul li {
  margin: 0 1rem;
}

@media (width <= 768px) {
  nav ul li {
    margin: .5rem 0;
  }
}

nav ul li a {
  color: #333;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s;
}

nav ul li a:hover {
  color: #21344b;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #21344b;
  margin-top: 3rem;
  padding: 2rem 0;
}

.social-icons {
  margin-top: 1rem;
}

.social-icons span {
  margin: 0 .5rem;
  font-size: 1.5rem;
}

.recipe-link {
  color: #fff;
  background-color: #21344b;
  border-radius: 5px;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.recipe-link:hover {
  background-color: #ce472f;
}

.btn {
  cursor: pointer;
  text-align: center;
  border: none;
  border-radius: 5px;
  padding: .5rem 1rem;
  font-weight: bold;
  transition: all .3s;
  display: inline-block;
}

.btn--primary {
  color: #fff;
  background-color: #21344b;
}

.btn--primary:hover {
  background-color: #111b28;
}

.btn--secondary {
  color: #333;
  background-color: #78b9ba;
}

.btn--secondary:hover {
  background-color: #56a8a9;
}

.btn--accent {
  color: #fff;
  background-color: #ce472f;
}

.btn--accent:hover {
  background-color: #a43926;
}

.recipe-card {
  background-color: #fff;
  border-radius: 10px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 3px 10px #0000001a;
}

.recipe-card:hover {
  transform: translateY(-5px);
}

.recipe-card .recipe-image {
  background-color: #ddd;
  background-position: center;
  background-size: cover;
  height: 200px;
}

.recipe-card .recipe-content {
  padding: 1.5rem;
}

.recipe-card .recipe-title {
  color: #21344b;
  margin-bottom: .5rem;
  font-size: 1.3rem;
}

.recipe-card .recipe-movie {
  color: #ce472f;
  margin-bottom: .5rem;
  font-style: italic;
}

.recipe-card .recipe-description {
  margin-bottom: 1rem;
}

.featured-recipes {
  margin-bottom: 3rem;
}

.about-section {
  margin: 3rem 0;
}

.about-section .about-content {
  flex-wrap: wrap;
  align-items: center;
  gap: 2rem;
  display: flex;
}

@media (width <= 768px) {
  .about-section .about-content {
    flex-direction: column;
  }
}

.about-section .about-content .about-text, .about-section .about-content .about-image {
  flex: 1;
  min-width: 300px;
}

.about-section .about-content .about-image img {
  border-radius: 10px;
  width: 100%;
}

.newsletter {
  text-align: center;
  background-color: #78b9ba;
  border-radius: 10px;
  margin: 3rem 0;
  padding: 3rem 0;
}

.newsletter h2 {
  color: #21344b;
  margin-bottom: 1rem;
}

.newsletter p {
  max-width: 600px;
  margin-bottom: 2rem;
  margin-left: auto;
  margin-right: auto;
}

.newsletter form {
  max-width: 500px;
  margin: 0 auto;
  display: flex;
}

@media (width <= 768px) {
  .newsletter form {
    flex-direction: column;
  }
}

.newsletter form input {
  border: none;
  border-radius: 5px 0 0 5px;
  flex-grow: 1;
  padding: .75rem;
}

@media (width <= 768px) {
  .newsletter form input {
    border-radius: 5px;
    margin-bottom: 1rem;
  }
}

.newsletter form button {
  color: #fff;
  cursor: pointer;
  background-color: #21344b;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: .75rem 1.5rem;
}

@media (width <= 768px) {
  .newsletter form button {
    border-radius: 5px;
  }
}

.newsletter form button:hover {
  background-color: #111b28;
}

.category-header {
  text-align: center;
  margin-bottom: 2rem;
}

.category-header h1 {
  color: #21344b;
  margin-bottom: .5rem;
}

.category-header p {
  max-width: 800px;
  margin: 0 auto;
}

.filter-container {
  text-align: center;
  margin-bottom: 2rem;
}

.filter-container .filter-button {
  color: #333;
  cursor: pointer;
  background-color: #78b9ba;
  border: none;
  border-radius: 5px;
  margin: 0 .5rem .5rem 0;
  padding: .5rem 1rem;
  transition: all .3s;
}

.filter-container .filter-button:hover {
  background-color: #56a8a9;
}

.filter-container .filter-button.active {
  color: #fff;
  background-color: #21344b;
}

.pagination {
  justify-content: center;
  margin-top: 2rem;
  display: flex;
}

.pagination .page-item {
  margin: 0 .25rem;
}

.pagination .page-item .page-link {
  color: #21344b;
  background-color: #fff;
  border-radius: 5px;
  padding: .5rem 1rem;
  transition: all .3s;
  display: inline-block;
}

.pagination .page-item .page-link:hover, .pagination .page-item.active .page-link {
  color: #fff;
  background-color: #21344b;
}

.recipe-detail {
  background-color: #fff;
  border-radius: 10px;
  margin-bottom: 3rem;
  overflow: hidden;
  box-shadow: 0 3px 10px #0000001a;
}

.recipe-detail .recipe-hero {
  height: 400px;
  position: relative;
}

.recipe-detail .recipe-hero .recipe-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.recipe-detail .recipe-hero .recipe-title-container {
  color: #fff;
  background: linear-gradient(to top, #000c, #0000);
  padding: 2rem;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.recipe-detail .recipe-hero .recipe-title-container h1 {
  color: #fff;
  margin-bottom: .5rem;
}

.recipe-detail .recipe-hero .recipe-title-container .recipe-meta {
  gap: 2rem;
  display: flex;
}

.recipe-detail .recipe-hero .recipe-title-container .recipe-meta span {
  align-items: center;
  display: flex;
}

.recipe-detail .recipe-hero .recipe-title-container .recipe-meta span i {
  margin-right: .5rem;
}

.recipe-detail .recipe-content {
  padding: 2rem;
}

.recipe-detail .recipe-content .recipe-description, .recipe-detail .recipe-content .recipe-section {
  margin-bottom: 2rem;
}

.recipe-detail .recipe-content .recipe-section h2 {
  border-bottom: 2px solid #78b9ba;
  margin-bottom: 1rem;
  padding-bottom: .5rem;
}

.recipe-detail .recipe-content .ingredients-list {
  margin-bottom: 2rem;
  list-style-position: inside;
}

.recipe-detail .recipe-content .ingredients-list li {
  margin-bottom: .5rem;
}

.recipe-detail .recipe-content .steps-list {
  counter-reset: step-counter;
  list-style-type: none;
}

.recipe-detail .recipe-content .steps-list li {
  margin-bottom: 1.5rem;
  padding-left: 3rem;
  position: relative;
}

.recipe-detail .recipe-content .steps-list li:before {
  content: counter(step-counter);
  counter-increment: step-counter;
  color: #fff;
  background-color: #21344b;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  font-weight: bold;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.recipe-detail .recipe-content .recipe-video {
  margin: 2rem 0;
}

.recipe-detail .recipe-content .recipe-video iframe {
  aspect-ratio: 16 / 9;
  border: none;
  border-radius: 10px;
  width: 100%;
}

.recipe-detail .recipe-content .recipe-gallery {
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
  margin: 2rem 0;
  display: grid;
}

.recipe-detail .recipe-content .recipe-gallery img {
  object-fit: cover;
  border-radius: 5px;
  width: 100%;
  height: 200px;
  transition: transform .3s;
}

.recipe-detail .recipe-content .recipe-gallery img:hover {
  transform: scale(1.05);
}

.related-recipes {
  margin-top: 3rem;
}

.back-link {
  color: #21344b;
  margin-bottom: 2rem;
  display: inline-block;
}

.back-link i {
  margin-right: .5rem;
}

.back-link:hover {
  color: #ce472f;
}
